import $ from 'jquery';
import render from '../datatable/render';
/**
 * Documentenketzerjavascriptcode.
 */
$(async () => {
    await import(/*webpackChunkName: "datatable"*/ '../datatable/bootstrap');
    const $documenten = $('#documenten');
    // tabellen naar zebra converteren.
    $documenten.find('tr:odd').addClass('odd');
    // render de filesize cellen
    $documenten.find('.size')
        .each((i, el) => el.innerText = render.filesize(el.innerText, 'display', null, {}));
    $('#documentencategorie').DataTable({
        columns: [
            { type: 'html' },
            { type: 'num', render: render.filesize },
            { type: 'string' },
            { render: render.timeago },
            {},
        ],
        info: false,
        language: {
            infoEmpty: 'Geen documenten gevonden',
            search: 'Zoeken:',
            zeroRecords: 'Geen documenten gevonden',
        },
        lengthChange: false,
        order: [[3, 'desc']],
        pageLength: 20,
    });
});
